.mybox {
  height: 500px;
  background: #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mybox .box {
  margin-right: 10px;
  width: 100px;
  background-color: #09f;
}
.mybox .box:nth-child(1) {
  width: 200px;
}
.mybox .box:nth-child(2) {
  flex: 1 1;
  align-self: stretch;
}
.mybox .box:nth-child(3) {
  flex: 1.5 1;
  align-self: center;
  width: 100px;
  height: 100px;
}
.mybox .box:last-child {
  width: 200px;
  background: green;
  align-self: flex-start;
}
