.header {
  background-color: #fff;
}
.header .header-top {
  padding: 0 20px;
  height: 60px;
  line-height: 60px;
  text-align: right;
}
.header .header-top .logo {
  line-height: 60px;
}
.header .header-top .logo img {
  width: 40px;
  vertical-align: middle;
  margin: -3px 10px 0px 0px;
}
.header .header-top a {
  margin-left: 1em;
}
.header .breadcrumb {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  border-top: 1px solid #40a9ff;
}
.header .breadcrumb .breadcrumb-title {
  text-align: center;
}
.header .breadcrumb .breadcrumb-title:after {
  position: absolute;
  content: "";
  left: 50%;
  transform: translateX(-50%);
  bottom: -8px;
  width: 10px;
  height: 10px;
  border-top: 9px solid #fff;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
}
.header .breadcrumb .weather {
  text-align: right;
  font-size: 14px;
}
.header .breadcrumb .weather .weather-img img {
  height: 22px;
}
.header .breadcrumb .weather .weather-detail {
  margin-left: 5px;
}
.simple-page .header-top {
  background-color: #09f;
  color: white;
}
.simple-page .header-top a {
  color: white;
}
